.nav-container {
  background-color: hsl(0, 25%, 97%);
  border-bottom: 0.2em solid #222;
  /* max-width: 100%; */
}

.nav-container__inner {
  display: flex;
  justify-content: space-between;
  max-width: 1080px;
  margin: 0 auto;
  padding: 0.625em 0;
}

.home-link {
  color: #222;
  font-size: 1.6rem;
  font-family: Georgia, serif;
  font-weight: bold;
  text-decoration: none;
  margin-left: 0.5em;
}

.top-nav {
  display: flex;
  list-style-type: none;
  margin: 0;
}

.top-nav button,
.top-nav a {
  font-family: Segoe UI;
  font-size: 1rem;
  display: block;
  padding: 0.3em 0.75em;
  color: hsl(0, 25%, 97%);
  text-decoration: none;
  border-radius: 3px;
  border: 0;
  background-color: #222;
  margin-right: 1em;
  margin-top: 0.1em;
  cursor: pointer;
}

.top-nav a:hover,
.top-nav button:hover {
  color: #444;
}

@media (min-width: 35em) {
  .nav-container {
    background-color: hsl(0, 25%, 97%);
  }

  .nav-container__inner {
    display: flex;
    justify-content: space-between;
    max-width: 1080px;
    margin: 0 auto;
    padding: 0.625em 0;
  }

  .home-link {
    color: #222;
    font-size: 1.6rem;
    font-family: Georgia, serif;
    font-weight: bold;
    text-decoration: none;
  }

  .top-nav {
    display: flex;
    list-style-type: none;
    margin: 0;
  }

  .top-nav > li + li {
    margin-left: 0.625em;
  }

  .top-nav a:hover,
  .top-nav button:hover {
    color: #444;
  }
}
