.hero {
  background-image: radial-gradient(hsl(300, 80%, 40%), hsl(290, 80%, 30%));
  /* background-color: hsl(0, 25%, 97%); */
  /* padding: 35vh; */
  padding: 10em 12.5em 12.5em 0;
}

.hero__inner {
  max-width: 1080px;
  width: 90vw;
  margin-right: 2em;
  text-align: right;
}

.hero__cta,
.hero__cta:visited {
  /* display: inline-block;
  color: hsl(0, 25%, 97%);
  text-decoration: none;
  font-size: 1.95rem;
  letter-spacing: -0.02em;
  margin-top: 0;
  margin-bottom: 0.625rem; */

  display: inline-block;
  color: hsl(0, 25%, 97%);
  border-radius: 0.2em;
  text-decoration: none;
  font-size: 1rem;

  padding: 0.6em 1em;
  background-color: #939;
  border: none;

  transition: color 0.05s ease-out;
}

.hero__cta:hover {
  color: hsl(0, 25%, 90%);
  background-color: #938;
}

.hero__inner h2 {
  margin: 0;
  margin-bottom: 0.75rem;
  font-size: 1.7rem;

  color: hsl(310, 35%, 95%);
}

.hero__select {
  display: inline-block;
  text-align: left;
  margin-right: 1.5em;
}

@media (min-width: 35em) {
  .hero__inner {
    margin: 0 auto;
  }

  .hero__inner h2 {
    font-size: 1.95rem;
  }
}
